.profile__name {
  font-style: normal;
  font-weight: 500;
  font-size: 42px;
  margin: 0;
  margin-bottom: 10px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media screen and (max-width: 688px) {
  .profile__name {
    font-size: 27px;
    margin-top: 28px;
  }
}
